import Photo from '../../assets/photoabout.webp'

export default function Apropos() {


    return (
        <div id="point_apropos">

            <div className='apropos'>
                <>
                    <h1 className='h1_title'>ABOUT</h1>
                    <div className="loader_first">
                        <span className=" loader_one "></span>
                    </div>
                </>
                <p className='p_about'>
                    <div className="image-wrapper">
                        <img src={Photo} alt="photo de ligne de code" className='Photo_About'></img>
                        <div className="circle">  &lt;/&gt;</div>
                    </div>
                    Infographiste indpendant depuis 2001, voulant me lancer un nouveau dfit professionnel, je me suis reconverti en tant que Dveloppeur Frontend.<br /><br />

                    Jai suivi la formation certifiante : Intgrateur Web niveau Bac +2 dOpenclassrooms durant laquelle jai appris et acquis de solides comptences en HTML, CSS, Javascript et React.<br /><br />
                    Au cours de ces mois, jai ralis une multitude de projets diversifis afin de me confronter  diffrentes situations allant de petites applications web de base  des systmes beaucoup plus complexes.
                    En tant que dveloppeur, je mattache particulirement  lefficacit et la qualit du code, tout en restant en alerte des nouveauts par une veille technique.<br />
                </p>
            </div>
        </div>

    )
}